.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header .logo {
  width: 40px;
  height: 40px;
  display: inline-block;
}

.header .nav {
  display: block;
}

:root {
  --blue-color: #00acdc;
  --white-smoke-color: #f3f3f3;
  --white-color: #fff;
  --black-color: #060b35;
  --gray-color: #616070;
  --st-tropaz-color: #34568b;
}

.rating {
  justify-content: space-between;
  width: 83%;
  margin: 16px 0 8px 16px;
  display: flex;
}

.rating .stars {
  justify-content: space-between;
  width: 96px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.rating .stars .stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
}

.rating .stars--1 :nth-child(-n+1), .rating .stars--2 :nth-child(-n+2), .rating .stars--3 :nth-child(-n+3), .rating .stars--4 :nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.rating .reviews5 {
  height: 16px;
  margin: 0 0 0 17px;
  padding: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__list .nav__item {
  padding-right: 20px;
}

.nav__list .nav__item .nav__link {
  text-align: center;
  color: var(--black-color);
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__list .nav__item .is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.nav__list .nav__item .nav__link:hover, .nav__list .nav__item .nav__link.is-active {
  color: var(--blue-color);
}

.nav__list .nav__item:last-child {
  padding-right: 0;
}

html {
  --card-height: 406px;
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

body .main {
  grid-template-rows: 1fr 1fr;
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  height: 100%;
  padding: 50px 40px;
  display: grid;
}

body .main .card {
  height: var(--card-height);
  background-color: var(--white-color);
  border: 1px solid;
  border-color: var(--white-smoke-color);
  border-radius: 5px;
  flex-direction: column;
  width: 198px;
  transition: transform .3s;
  display: flex;
}

body .main .card .photo {
  width: 160px;
  height: 134px;
  margin: 32px 19px 40px;
}

body .main .card .info {
  text-align: left;
  color: var(--black-color);
  height: 36px;
  margin: 0 16px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  text-decoration: none;
  left: 17px;
}

body .main .card .code {
  text-align: left;
  color: var(--gray-color);
  margin: 4px 16px 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

body .main .card .price {
  justify-content: space-between;
  height: 18px;
  margin: 16px 16px 8px;
  display: flex;
}

body .main .card .price .price__price {
  text-align: left;
  color: var(--gray-color);
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

body .main .card .price .price___sum {
  text-align: right;
  color: var(--black-color);
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

body .main .card .button {
  text-align: center;
  text-transform: uppercase;
  color: var(--white-color);
  background-color: var(--blue-color);
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin: 8px 16px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

body .main .card .button .buy {
  margin: 12px;
}

body .main .card .button:hover {
  text-align: center;
  text-transform: uppercase;
  color: var(--blue-color);
  size: 14px;
  background-color: var(--white-color);
  border: 1px solid;
  border-color: var(--blue-color);
  border-radius: 5px;
  width: 164px;
  height: 38px;
  margin: 8px 16px 0;
  font-weight: 700;
  line-height: 16px;
}

body .card:hover {
  transform: scale(1.2);
}

body .card:hover .info {
  color: var(--st-tropaz-color);
}

@media (width >= 488px) {
  body .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  body .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  body .main {
    grid-template-columns: repeat(4, 200px);
  }
}

@media (width >= 1200px) {
  body .main {
    padding: 50px 128px;
  }
}
